  left: 50%;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
}
.neb.neb-sw::before {
  top: 100%;
  left: 0.625rem;
  transform: translateY(-50%) rotate(45deg);
}
.neb.neb-se::before {
