.selected {
  box-shadow: 0 0 0 4px #5ebdec !important;
}
.selected::after {
  position: absolute;
  display: block;
  border-top: 28px solid #5ebdec;
  border-left: 28px solid transparent;
  right: -1px;
  content: "";
  top: -1px;
  z-index: 100;
}
.selected::before {
  position: absolute;
  display: block;
  content: "";
  background-color: transparent;
  border-color: #ffffff !important;
  border-left: 2px solid;
  border-bottom: 2px solid;
  height: .325rem;
  width: .5rem;
  right: .25rem;
  top: .25rem;
  z-index: 102;
  transform: rotate(-45deg);
}
.dropdown-toggle {
  position: relative;
  cursor: pointer;
  padding-right: 1.5rem!important;
  user-select: none;
}
.dropdown-toggle::before {
  display: block;
  position: absolute;
  vertical-align: middle;
  color: transparent;
  font-size: 0;
  content: "";
  height: 0.3125rem;
  width: 0.3125rem;
  background-color: transparent;
  border-left: 1px solid;
  border-bottom: 1px solid;
  border-color: #1d1d1d;
  top: 50%;
  left: 100%;
  margin-left: -1rem;
  margin-top: -0.1625rem;
  z-index: 2;
  transform: rotate(-45deg);
  transition: all 0.15s ease-in-out;
  transform-origin: center center 1px;
}
.dropdown-toggle.drop-marker-light::before,
.dropdown-toggle.marker-light::before {
  border-color: #ffffff;
}
.dropdown-toggle.no-marker::before {
  display: none;
}
.dropdown-toggle.active-toggle::before {
  transform: rotate(135deg);
  transition: all 0.15s ease-in-out;
}
.dropdown-toggle.marker-center::before {
  left: 50%;
  top: 50%;
  margin-left: 0;
  margin-top: 0;
  transform: translateX(-50%) translateY(-50%) rotate(-45deg);
}
.dropdown-toggle.marker-center.active-toggle::before {
  transform: translateX(-50%) translateY(-50%) rotate(135deg);
  transition: all 0.15s ease-in-out;
}
.disabled {
  pointer-events: none!important;
