.tiles-group {
  width: auto;
  float: left;
  overflow: visible;
  margin-left: 80px;
}
.tiles-group::before {
  content: attr(data-group-title);
  display: block;
  position: absolute;
  left: 0;
  top: -36px;
  height: 1.5em;
  line-height: 1.5em;
  z-index: 1;
  color: inherit;
}
[class*=tile-].image-set .img {
  width: 25%;
  height: 50%;
  display: block;
  float: left;
  border: 1px solid #1d1d1d;
  background-size: cover;
}
[class*=tile-].image-set .img:nth-child(1) {
  width: 50%;
  height: 100%;
}
[class*=tile-] .slide {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
}
[class*=tile-] [class*=slide-] {
  width: 100%;
  height: 100%;
  position: absolute;
  transition: all 0.3s ease-in-out;
}
[class*=tile-] .slide-front {
  top: 0;
  left: 0;
}
[class*=tile-].effect-hover-slide-up .slide-back,
[class*=tile-].effect-hover-zoom-up .slide-back {
  top: 100%;
  left: 0;
