  position: absolute;
  content: '\2605';
  display: block;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  top: 50%;
}
.rating .result {
  font-size: .8em;
  margin-left: 10px;
}
.rating .title {
  margin-right: 10px;
}
